
* {
    box-sizing: border-box;
}

::selection {
    background-color: lighten($clr1, 20%);
    color: $clr6;
}

html {
    font-family: livic-semi-bold,sans-serif;
}

body {
    margin: 0;
    padding: 0;
    background-color: #014e56;
    color: #e8eaed;
    width: 100vw;
}

section.app-layout{
    width: 100vw;
    // margin: 0px 165px;
}

button:hover {
    cursor: pointer;
}

img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

h1,h2,h3,h4,h5,h6 {
    line-height: 1.2;
    margin: 0 0 em(20px, 50px);
}

h1 {
    font-size: rem(50px);
}

p {
    margin: 0;
    max-width: 50ch;
}

a.clean-link{
    color: #e8eaed;
}



