article.reviews{
    width: 100%;
    min-height: 745px;
    flex-direction: column;

    h1{
        width: 932px;
        height: 100px;
        color: #ffffff;
        font-family: "livic-bold";
        font-size: 48px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 48px;
        text-align: center;
        margin: 140px 0px 154px 0px;
        max-width: 17ch;
       span{
        color: #79c8c7;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
       }
        
    }

    .reviews-container{
        flex-direction: column;
     
        div.review-card:not(:last-child){
            margin-right: 30px;
        }
        div.review-card:last-child{
            margin-bottom: 164px;
        }
        .review-card{
            height: 302px;
            width: 350px;
            margin-bottom: 48px;
            

            .quotes-svg-container{
                height: 56px;
                width: 67px;
                position: relative;
                z-index: 1;
                left:141px;
                top: 20px;
                right: 141px;
            }

            p{
                height: 96px;
                z-index: 10;
                color: #ffffff;
                font-size: 15px;
                font-weight: 600;
                position: relative;
                font-style: normal;
                letter-spacing: normal;
                line-height: 25px;
                text-align: center;
                margin: 0px 0px 33px 0px;
                padding: 0;
            }
            h5{
                height: 28px;
                color: #79c8c7;
                font-family: livic-bold,sans-serif;
                font-size: 18px;
                font-weight: 700;
                font-style: normal;
                letter-spacing: normal;
                line-height: 28px;
                text-align: center;
                margin: 0;
                padding: 0;
                margin: 0px 0px 10px 0px;
            }
            h6{
                height: 18px;
                color: #ffffff;
                font-family: livic-bold-italic,sans-serif;
                font-size: 13px;
                font-weight: 500;
                font-style: normal;
                letter-spacing: normal;
                line-height: 18px;
                text-align: center;
                margin: 0px 0px 33px 0px;
            }
            .avatar-container{
                width: 62px;
                height: 62px;
                border: 2px solid #c4fffe;
                margin: auto;
                border-radius: 50%;
            }
        }

    }
        
}

@media (min-width: 640px) {
    article.reviews{
        h1{
            margin: 140px 0px 80px 0px;
            max-width: fit-content;
        }
        .reviews-container{
            flex-direction: row;

            div.review-card:last-child{
                margin-bottom: 0px;
            }
            
        }
  }
}
  
  