article.main-hero{
    width: 100%;
    height: 552px;
    flex-direction: column;
    justify-content: space-between;
    padding: 0px 24px;

    .hero-svg{
        width: 100%;
        justify-self: flex-end;
        align-self: center;
    }

    .hero-header{
        flex-direction: column;
        align-items: center;
        gap:0px;
        h1{
            width: 237px;
            height: 201px;
            color: #ffffff;
            font-family: livic-bold,sans-serif;
            font-size: 50px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: normal;
            line-height: 50px;
            text-align: left;
            span{
                color: #f67e7e;
                font-style: normal;
                letter-spacing: normal;
                line-height: normal;
            }
        }
        p{
            width: 445px;
            height: 109px;
            color: #ffffff;
            font-size: 18px;
            font-weight: 600;
            font-style: normal;
            letter-spacing: normal;
            line-height: 28px;
            text-align: left;
            margin: 0px 0px 9px 0px;

        }
    }
}


@media (min-width: 992px) {
    article.main-hero{
        padding: 0px 165px;

        .hero-svg{
            width: 315px;
            align-self:flex-end;
            justify-self: flex-end;
            align-self:flex-end;
        }

        .hero-header{
            flex-direction: row;
            gap:30px;

            h1{
                width: 635px;
                font-size: 100px;
                line-height: 100px;
            }

            p{
                margin: 83px 0px 9px 0px;
            }
        }
    }
}
  